import { useState, useEffect} from "react";
import {  getDataFromStruct } from "../data/handle.js";

const FormScreen = props => {
    const [food, setFood] = useState(0);
    const [sport, setSport] = useState(0);
    const [alcool, setAlcool] = useState(0);
    const [tabac, setTabac] = useState(0);
    const [sleep, setSleep] = useState(0);
    const [mood, setMood] = useState(0);
    const [weight, setWeight] = useState(0);

    useEffect(() => {
        getDataFromStruct(props.db.d2, props.date, props.password)
            .then(function(data){
                setFood(data.f);
                setSport(data.s);
                setAlcool(data.a);
                setTabac(data.t);
                setSleep(data.sl);
                setMood(data.m);
                setWeight(data.w);
            })
      }, [props.db.d2, props.date, props.password]);

    const handleCommitClick = () => {
        console.log("commit clik");
        props.commitCallback();
    }

    const handleAddClick = () => {
        const data = {
            food: parseInt(food), 
            sport: parseInt(sport), 
            alcool: parseInt(alcool), 
            tabac: parseInt(tabac), 
            sleep: parseInt(sleep),
            mood: parseInt(mood),
            weight: parseInt(weight),
            date: props.date
        }
        props.dataHandle(data);
    }

    return (
        <div>
            <p>{props.date.toLocaleDateString('en-GB')}</p> 
            <div>
                Food: 
                <input type="radio" value="1" name="food" checked={food === 1} onChange={(e) => setFood(parseInt(e.target.value))}/>1
                <input type="radio" value="2" name="food" checked={food === 2} onChange={(e) => setFood(parseInt(e.target.value))}/>2
                <input type="radio" value="3" name="food" checked={food === 3} onChange={(e) => setFood(parseInt(e.target.value))}/> 3
            </div>
            <div>
                Sport: 
                <input type="radio" value="1" name="sport" checked={sport === 1} onChange={(e) => setSport(parseInt(e.target.value))}/> 1
                <input type="radio" value="2" name="sport" checked={sport === 2} onChange={(e) => setSport(parseInt(e.target.value))}/> 2
                <input type="radio" value="3" name="sport" checked={sport === 3} onChange={(e) => setSport(parseInt(e.target.value))}/> 3
            </div>
            <div>
                Alcool: 
                <input type="radio" value="1" name="alcool" checked={alcool === 1} onChange={(e) => setAlcool(parseInt(e.target.value))}/> 1
                <input type="radio" value="2" name="alcool" checked={alcool === 2} onChange={(e) => setAlcool(parseInt(e.target.value))}/> 2
                <input type="radio" value="3" name="alcool" checked={alcool === 3} onChange={(e) => setAlcool(parseInt(e.target.value))}/> 3
            </div>
            <div>
                Tabac: 
                <input type="radio" value="1" name="tabac" checked={tabac === 1} onChange={(e) => setTabac(parseInt(e.target.value))}/> 1
                <input type="radio" value="2" name="tabac" checked={tabac === 2} onChange={(e) => setTabac(parseInt(e.target.value))}/> 2
            </div>
            <div>
                Sleep: 
                <input type="radio" value="1" name="sleep" checked={sleep === 1} onChange={(e) => setSleep(parseInt(e.target.value))}/> 1
                <input type="radio" value="2" name="sleep" checked={sleep === 2} onChange={(e) => setSleep(parseInt(e.target.value))}/> 2
            </div>
            <div>
                Mood: 
                <input type="radio" value="1" name="mood" checked={mood === 1} onChange={(e) => setMood(parseInt(e.target.value))}/> 1
                <input type="radio" value="2" name="mood" checked={mood === 2} onChange={(e) => setMood(parseInt(e.target.value))}/> 2
            </div>
            <div>
                Weight: 
                <input type="text" name="weight" value={weight.toString()} onChange={(e) => setWeight(parseInt(e.target.value))}/>
            </div>
            <button onClick={handleAddClick}>Add</button>
            <br/>
            <button onClick={handleCommitClick}>Commit</button>
        </div>
    );
}

export default FormScreen;
