import { useState, useEffect } from "react";
import { decryptData} from "./enc/crypt.js"
import {commitFile} from "./gh/gh.js"
import Login from "./Login/Login"
import EditScreen from "./Screens/EditScreen"
import { addDataToStruct } from "./data/handle.js";

function App() {
  const [password, setPassword] = useState("");
  const [db, setDb] = useState("");
  const [loggedIn, setLoggedIn] = useState("false");

  const commitCallback = () => {
    const repository = "sandrolex/sandrolex.github.io";
    const branch = "main";
    const filePath = "db.json";
    const content = JSON.stringify(db);
    const pat = decryptData(db.d1, password);
    commitFile(repository, branch, filePath, content, pat)
  }

  const dataHandle = (data) => {
    db.d2 = addDataToStruct(db.d2, data, password);
  }

  const loginCallback = (data) => {
    setPassword(data);
    const verif = decryptData(db.d3, data);
    if (verif === "true") {
      setLoggedIn("true");
    } else {
      console.log("failed")
    }
  }

  useEffect(() => {
    const host = window.location.host;
    let protocol = "http://";
    if (host.includes("github") ) {
      protocol = "https://";
    }
    const dbUrl = protocol + host + '/db.json';
    fetch(dbUrl).then(
		  function (res) {
			  return res.json();
		  },
    ).then(function (data) {
      // store Data in State db Variable
      setDb(data);
    }).catch(
      function (err) {
        console.log(err, ' error');
      },
    );
  }, []);
    
  if (loggedIn === "false") {
    return (
      <Login loginCallback={loginCallback} d3={db.d3}/>
    )
  } else {
    return (
    <div className="container">
      <EditScreen 
          dataHandle={dataHandle} 
          commitCallback={commitCallback} 
          password={password}
          db={db}
        />
    </div>
    );
  }
}

export default App;


